<template>
      <div class="eol-table-box-right">
        <div class="eol-table-box-right-inner">
          <div class="eol-table-container box-shadow-style-2">
            <!---->
            <div class="modelTable" v-show="tableVisible">
              <!---->
              <div id="form-model">
                <el-form :inline="true" :model="formInline" class="demo-form-inline">
                  <el-form-item label="ID">
                    <el-input v-model="formInline.id" placeholder="ID"></el-input>
                  </el-form-item>
                  <el-form-item label="">
                    <el-select v-model="formInline.region" placeholder="">
                      <el-option label="1" value="shanghai"></el-option>
                      <el-option label="2" value="beijing"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item>
                    <el-button type="primary" size="small  " @click="onSubmit"></el-button>
                  </el-form-item>
                </el-form>
              </div>

              <!---->
              <div class="model-btn">
                <el-button type="primary" size="small" @click="isShowTable"></el-button>
                <el-button type="primary" size="small"></el-button>
              </div>

              <!---->
              <div class="modelList-table" style="">
                  <template>
                    <el-table
                      ref="multipleTable"
                      :data="tableData3"
                      tooltip-effect="dark"
                      max-height="700"
                      style="width: 100%"
                      @selection-change="handleSelectionChange">
                      <el-table-column
                        type="selection"
                        align="center"
                        width="60">
                      </el-table-column>
                      <el-table-column
                        label="ID"
                        align="center"
                        width="120">
                        <template slot-scope="scope">{{ scope.row.id }}</template>
                      </el-table-column>
                      <el-table-column
                        prop="name"
                        label=""
                        align="center"
                        width="120">
                      </el-table-column>
                      <el-table-column
                        prop="type"
                        label=""
                        align="center">
                      </el-table-column>
                      <el-table-column
                        prop="versionNumber"
                        label=""
                        align="center">
                      </el-table-column>
                      <el-table-column
                        prop="graphical"
                        label=""
                        align="center">
                      </el-table-column>
                      <el-table-column
                        prop="deploymentTime"
                        label=""
                        align="center">
                      </el-table-column>
                      <el-table-column
                        label=""
                        width="180"
                        align="center">
                        <template>
                          <img class="margin-right-3px pointer" src="../../../assets/images/edit.png" alt="">
                          <img class="margin-right-3px pointer" src="../../../assets/images/deploy.png" alt="">
                          <img class="margin-right-3px pointer" src="../../../assets/images/import.png" alt="">
                          <img class="margin-right-3px pointer" src="../../../assets/images/export.png" alt="">
                          <img class="pointer" src="../../../assets/images/delete.png" alt="">
                        </template>
                      </el-table-column>
                    </el-table>
                  </template>
              </div>

              <!---->
              <div class="pagination-block" v-show="tableVisible">
                    <el-pagination
                      @size-change="handleSizeChange"
                      @current-change="handleCurrentChange"
                      :current-page="currentPage"
                      :page-sizes="[100, 200, 300, 400]"
                      :page-size="100"
                      layout="total, sizes, prev, pager, next, jumper"
                      :total="400">
                    </el-pagination>
              </div>

            </div>

            <!---->
            <div class="processEngine" v-show="fromVisible">
               <!---->
              <div class="processEngineModel-btn">
                <!-- <el-button type="primary"  size="small" @click="isShowTable"></el-button> -->
                <el-button type="primary"  size="small" @click="retrunTable"></el-button>
              </div>

              <div class="iframe" style="padding:0px 20px;height:100%;">
                <!-- <iframe ref="iframe"  src="../../../../static/processDesigner/index.html" width="100%" height="100%"></iframe> -->
                <iframe ref="iframe" src="/workflow/front-resource/processDesigner/index.html" width="100%" height="100%"></iframe>
              </div>
            </div>

          </div>
          
        </div>
      </div>
</template>


<script>
  
  export default {
    components:{
    
    },
    data() {
      return {
        currentPage: 1,
        formInline: {
          id: '',
          region: ''
        },
        //
        tableData3: [{
          id: '001',
          name: 'B5',
          type: '',
          versionNumber:'1',
          graphical:'01',
          deploymentTime:'2019-2-15',
        },{
          id: '002',
          name: 'B4',
          type: '',
          versionNumber:'2',
          graphical:'02',
          deploymentTime:'2019-2-18',
        },{
          id: '003',
          name: 'B5',
          type: '',
          versionNumber:'1',
          graphical:'01',
          deploymentTime:'2019-2-15',
        },{
          id: '004',
          name: 'B5',
          type: '',
          versionNumber:'1',
          graphical:'01',
          deploymentTime:'2019-2-16',
        },{
          id: '005',
          name: 'B5',
          type: '',
          versionNumber:'4',
          graphical:'05',
          deploymentTime:'2019-2-15',
        }, {
          id: '006',
          name: 'B5',
          type: '',
          versionNumber:'1',
          graphical:'06',
          deploymentTime:'2019-2-17',
        }, {
          id: '007',
          name: 'B5',
          type: '',
          versionNumber:'6',
          graphical:'07',
          deploymentTime:'2019-2-15',
        },{
          id: '008',
          name: 'B5',
          type: '',
          versionNumber:'6',
          graphical:'01',
          deploymentTime:'2019-2-20',
        },{
          id: '009',
          name: 'B5',
          type: '',
          versionNumber:'4',
          graphical:'01',
          deploymentTime:'2019-2-27',
        },{
          id: '010',
          name: 'B5',
          type: '',
          versionNumber:'2',
          graphical:'5',
          deploymentTime:'2019-2-15',
        },],
        //
        multipleSelection: []
      };
    },
    mounted() {
      
    },
    computed:{
      fromVisible:function() {
        // vuexgettersstate
        return this.$store.getters.getFromVisible;
      },
      tableVisible:function() {
        // vuexgettersstate
        return this.$store.getters.getRableVisible;
      },

    },
    methods: {
      //
      retrunTable(){
        //vuex
        // vuexations
        this.$store.dispatch("setFromVisible", false);
        this.$store.dispatch("settRableVisible", true);
      },
      //
      isShowTable(){
        // vuexations
        this.$store.dispatch("setFromVisible", true);
        this.$store.dispatch("settRableVisible", false);
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
      }
    }
  };
</script>


<style lang="less" scoped>
  .processEngine{
    width:100%;
    height: calc(100% - 65px);
    padding-top:10px;
    //
    .processEngineModel-btn{
      margin:0px 20px 10px 20px;
    }
  }
  .eol-table-box-right,
  .eol-table-box-right-inner,
  .eol-table-container{
      height:100%;
  }
  #form-model{
    padding:20px 20px 0px 20px;
    border-bottom: 1px solid #BAC4CC;
  }
  
  .modelTable{
    //
    .model-btn{
      padding:10px 20px;
    }
    
    //
    .modelList-table{
      padding:0px 20px;
    }
    //
    .pagination-block{
      margin:20px;
      padding:10px 0px;
      text-align: center;
      background: #e5f5fd;
    }
  }
</style>



