<template>
      <div class="eol-table-box-right">
        <div class="eol-table-box-right-inner">
          <div class="eol-table-container box-shadow-style-2">
            <div class="formTable" v-show="formTableVisible">
                <!---->
                <div id="form-model">
                    <el-form :inline="true" :model="formInline" class="demo-form-inline">
                        <el-form-item label="">
                        <el-input v-model="formInline.id" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="">
                        <el-select v-model="formInline.region" placeholder="">
                            <el-option label="1" value="shanghai"></el-option>
                            <el-option label="2" value="beijing"></el-option>
                        </el-select>
                        </el-form-item>
                        <el-form-item>
                        <el-button type="primary" size="small  " @click="onSubmit"></el-button>
                        </el-form-item>
                    </el-form>
                </div>

                <!---->
                <div class="model-btn">
                <el-button type="primary" size="small" @click="isShowTable"></el-button>
