                            align="center">
                            <template slot-scope="scope">{{ scope.row.title }}</template>
                        </el-table-column>
                        <el-table-column
                            prop="processName"
                            label=""
                            align="center">
                        </el-table-column>
                        <el-table-column
                            prop="processName"
                            label=""
                            width="250"
                            align="center">
                        </el-table-column>
                        <el-table-column
                            prop="status"
                            label=""
                            width="250"
                            align="center">
                        </el-table-column>
                        <el-table-column
                            label=""
                            width="200"
                            align="center">
                            <template>
                                <img class="margin-right-3px pointer" src="../../../assets/images/edit.png" alt="">
                                <img class="margin-right-3px pointer" src="../../../assets/images/deploy.png" alt="">
                                <img class="margin-right-3px pointer" src="../../../assets/images/import.png" alt="">
                                <img class="margin-right-3px pointer" src="../../../assets/images/export.png" alt="">
                                <img class="pointer" src="../../../assets/images/delete.png" alt="">
                            </template>
                        </el-table-column>
                        </el-table>
                    </template>
                
                    
