<style lang="less" scoped>

  .eol-table-box-right,
  .eol-table-box-right-inner,
  .eol-table-container{
      height:100%;
  }
  #form-model{
    padding:20px 20px 0px 20px;
    border-bottom: 1px solid #BAC4CC;
  }
  //
  .model-btn{
