import Vue from 'vue';
import Vuex from 'Vuex';
import * as types from './mutation-types';
Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    fromVisible: false,
    tableVisible:true,
    //
    formControlVisible: false,
    formTableVisible:true,
    // routeInfo:{}, // ,
    // username:localStorage.getItem('username') || '',
  },
  getters:{
    // statestate
    getFromVisible(state){
        return state.fromVisible;
    },
    getRableVisible(state){
      return state.tableVisible;
    },
    //
    getformControlVisible(state){
      return state.formControlVisible;
    },
    getformTableVisible(state){
      return state.formTableVisible;
    },

  },
  mutations:{
    // statestate
      // name
    setFrom(state, name){
      state.fromVisible = name;//statefromVisible
    },
    settRable(state, name){
      state.tableVisible = name;//statetableVisible
    },
    //
    settFomrModelForm(state, name){
      state.formControlVisible = name;//statefromVisible
    },
    settFomrModelRable(state, name){
      state.formTableVisible = name;//statetableVisible
    },
   
  },
   // api
  actions:{
     // 
    // {commit, state}
    // statestate
    // commitmutations 
    // name
    setFromVisible({commit,state}, name){
      // mutaions
      commit("setFrom", name);
    },
    settRableVisible({commit,state}, name){
      // mutaions
      commit("settRable", name);
    },
    //
    setformControlVisible({commit,state}, name){
      // mutaions
      commit("settFomrModelForm", name);
    },
    settformTableVisible({commit,state}, name){
      // mutaions
      commit("settFomrModelRable", name);
    },

  },
})
export default store;



