import Vue from 'vue'
import Router from 'vue-router'

//
let Home = () => import('../pages/Home/index.vue');//
let statisticalReport = () => import('../pages/Home/statisticalReport/index.vue');



//
let ProcessManage = () => import('../pages/ProcessManage/index.vue');
let ProcessInFormation = () => import('../pages/ProcessManage/ProcessInFormation/index.vue');
let InstanceMonitoring = () => import('../pages/ProcessManage/InstanceMonitoring/index.vue');
let TaskCenter = () => import('../pages/ProcessManage/TaskCenter/index.vue');

//
import ModelManage from '../pages/ModelManage/index.vue';//
import NewModel from '../pages/ModelManage/NewModel/index.vue';//
import DditModel from '../pages/ModelManage/DditModel/index.vue';//
import DelModel from '../pages/ModelManage/DelModel/index.vue';//
import DeploymentModel from '../pages/ModelManage/DeploymentModel/index.vue';//
import ModelImport from '../pages/ModelManage/ModelImport/index.vue';//
import ModelExport from '../pages/ModelManage/ModelExport/index.vue';//

//
import SystemManage from '../pages/SystemManage/index.vue'//
import OrganizatStructure from '../pages/SystemManage/OrganizatStructure/index.vue';//
import RoleManage from '../pages/SystemManage/RoleManage/index.vue'//
import UserManage from '../pages/SystemManage/UserManage/index.vue'//
import ShopCalendar from '../pages/SystemManage/ShopCalendar/index.vue'//
import DigitalDictionary from '../pages/SystemManage/DigitalDictionary/index.vue'//
import LabelManage from '../pages/SystemManage/LabelManage/index.vue'//
import SystemLog from '../pages/SystemManage/SystemLog/index.vue'//
import TenantManage from '../pages/SystemManage/TenantManage/index.vue'//
import PrivilegeManage from '../pages/SystemManage/PrivilegeManage/index.vue'//
import DataInitializat from '../pages/SystemManage/DataInitializat/index.vue'//
import IntegrateConfigurat from '../pages/SystemManage/IntegrateConfigurat/index.vue'//

//
import FormManage from '../pages/FormManage/index.vue'
import FormControl from '../pages/FormManage/FormControl/index.vue'//
import FormTable from '../pages/FormManage/FormTable/formtable.vue';//

import Login from '../pages/Login/index.vue';
import App from '../pages/App.vue';

Vue.use(Router);
export default new Router({
  routes: [
    {
      path: '/',
      component: App,
      children: [
        {
          path: '/', //
          component: Home,
          redirect:'Home',
        },
        //
        {
          path: '/Home', //
          component: Home,
          //
          children: [
            {
              path: '',
              component: statisticalReport,
            
            }
          ]
        },
        //
        {
          path: '/processManage', 
          component: ProcessManage,
          //
          children: [
            //
            {
              path: '',
              component: ProcessInFormation,
            },
            //
            {
              path: 'instanceMonitoring',
              component: InstanceMonitoring,
            },
            //
            {
              path: 'taskCenter',
              component: TaskCenter,
            }
          ]
        },
        //
        {
          path: '/modelManage', 
          component: ModelManage,
          meta: {
            routeInfo: [
              { name: '', path: '/ModelManage', },
              { name: '', path: '/ModelManage' }
            ]
          },
          children: [
            //
            {
              path: '/',
              component: NewModel,
              meta: {
                routeInfo: [
                  { name: '', path: '/ModelManage', },
                  { name: '', path: '/ModelManage' }
                ]
              },
            },
            //
            {
              path: 'editModel',
              component: DditModel,
            },
            //
            {
              path: 'delModel',
              component: DelModel,
            },
            //
            {
              path: 'deploymentModel',
              component: DeploymentModel,
            },
            //
            {
              path: 'modelImport',
              component: ModelImport,
            },
            //
            {
              path: 'modelExport',
              component: ModelExport,
            }
          ]
        },
        //
        {
          path: '/systemManage', //
          component: SystemManage,
          children: [
            //
            {
              path: '',
              component: OrganizatStructure,
            },
            //
            {
              path: 'roleManage',
              component: RoleManage,
            },
            //
            {
              path: 'userManage',
              component: UserManage,
            },
            //
            {
              path: 'shopCalendar',
              component: ShopCalendar,
            },
            //
            {
              path: 'digitalDictionary',
              component: DigitalDictionary,
            },
            //
            {
              path: 'labelManage',
              component: LabelManage,
            },
            //
            {
              path: 'systemLog',
              component: SystemLog,
            },
            //
            {
              path: 'tenantManage',
              component: TenantManage,
            },
            //
            {
              path: 'privilegeManage',
              component: PrivilegeManage,
            },
            //
            {
              path: 'dataInitializat',
              component: DataInitializat,
            },
            //
            {
              path: 'integrateConfigurat',
              component: IntegrateConfigurat,
            }
          ]
        },
        //
        {
          path: '/formManage', 
          component: FormManage,
          children: [
            {
              path: '',
              component: FormTable,
            }
          ]
        },
      ]
    },

    {
      path: '/login',
      component: Login
    }
  ],
  linkActiveClass: 'visited'
});



